@import url('https://fonts.googleapis.com/css?family=Roboto:400,600');
@import "_reset.scss";
@import "mixins.scss";

body {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;

}

.back-img {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.container {
  margin: auto;

}

section {
  padding: 0 20px;
}

.rect-outer {
  position: relative;
  .rect-inner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

  }
}

.video-player-demo {
  position: relative;
}

.controls {
  z-index: 100;
  position: absolute;
  left: 10px;
  right: 10px;
  bottom: 20px;
  display: flex;

  .play-pause {
    margin: 0 10px 0 0;
    cursor: pointer;
    background: transparent;
    border: 0;
    text-indent: 30px;
    transform: translateY(-50%);
    width: 30px;
    height: 32px;

    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      border: 0 solid transparent;
      border-width: 8px 0 8px 15px;
      border-left-color: #eee;
      width: 0;
      height: 0;
      transition: all 0.2s linear;
    }

    &::before {
      height: 16px;
    }

    &::after {
      left: 15px;
      top: 8px;
    }

    &.paused {

      &::before,
      &::after {
        border-width: 0 0 0 12px;
        height: 32px;
      }

      &::after {
        left: 18px;
        top: 0;
      }
    }
  }




  .progress {
    display: block;
    width: 100%;
    height: 20px;
    overflow: hidden;
  }

  .progress-bar {
    width: 0%;
    height: 100%;
    display: inline-block;
    background-color: #2a84cd;
  }
}

@import "partials/section-intro";


